import merge from 'lodash/merge';
import omit from 'lodash/omit';
import { SFItem } from '@Models/core/item'
import { CONTENT_TYPE_ACTIONS } from '@Models/content_types';

/**
 * @notice This file exports 2 classes.
 */

export class Action {
  constructor(json) {
    merge(this, json);
    this.running = false;
    this.error = false;
    if(this.lastExecuted) {
      this.lastExecuted = new Date(this.lastExecuted);
    }
  }
}

export class SNExtension extends SFItem {
  constructor(json) {
      super(json);
      if(json.actions) {
        this.actions = json.actions.map((action) => {
          return new Action(action);
        })
      }
      if(!this.actions) {
        this.actions = [];
      }
  }

  actionsWithContextForItem(item) {
    return this.actions.filter((action) => {
      return action.context === item.content_type || action.context === 'Item';
    })
  }

  mapContentToLocalProperties(content) {
    super.mapContentToLocalProperties(content)
    this.description = content.description;
    this.url = content.url;
    this.name = content.name;
    this.package_info = content.package_info;
    this.supported_types = content.supported_types;
    if(content.actions) {
      this.actions = content.actions.map((action) => {
        return new Action(action);
      })
    }
  }

  get content_type() {
    return CONTENT_TYPE_ACTIONS;
  }

  structureParams() {
    const params = {
      name: this.name,
      url: this.url,
      package_info: this.package_info,
      description: this.description,
      actions: this.actions.map((a) => {
        return omit(a, ['subrows', 'subactions'])
      }),
      supported_types: this.supported_types
    };

    const superParams = super.structureParams();
    Object.assign(superParams, params);
    return superParams;
  }
}
